@import 'components/button/button',
        'components/input/input',
        'components/textarea/textarea',
        'components/preview/preview',
        'pages/auth/auth',
        'components/link/link',
        'pages/errors/errors',
        'pages/profile/profile',
        'modules/modal/modal';

.scroll{scrollbar-width:thin;scrollbar-color:$color-grey}
.scroll::-webkit-scrollbar-track{background:$color-grey-transparent}
.scroll::-webkit-scrollbar-thumb{background-color:$color-grey-transparent}
.scroll-thin::-webkit-scrollbar{width:1px}
.scroll-bold::-webkit-scrollbar{width:5px}

body{
        margin: 0;
        padding: 0;
        header{
                position: fixed;
                width: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-around;
                z-index: 10000;
                background-color: $color-white;
                border-bottom: 1px solid $color-grey-transparent;
                a{
                        text-align: center;
                        border-right: 1px solid $color-grey-transparent;
                        &:last-child{
                                border-right: none;
                        }
                }
        }

        main{
                width: 100%;
                padding: 3rem 0;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
        }
}


